<template>
  <div>
    <h1>Listado de Notas</h1>

    <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged">
      {{mensaje.texto}}
    </b-alert>

    <hr>
    <div class="container">
      <form @submit.prevent="editarNota()" v-if="editar">
        <h3 class="text-center">Editar Nota</h3>
        <input type="text" placeholder="Ingrese un Codigo" class="form-control my-2" v-model="notaEditar.cod">
        <input type="text" placeholder="Ingrese un Nombre" class="form-control my-2" v-model="notaEditar.nombre">
        <input type="text" placeholder="Ingrese una descripcion" class="form-control my-2"
          v-model="notaEditar.descripcion">
        <b-button class="btn-sm btn-block mb-1 btn-warning" type="submit" @click="editarNota(notaEditar._id)">Guardar</b-button>
        <b-button class="btn-sm btn-block"  @click="editar = false">Cancelar</b-button>
      </form>
    </div>
    <div class="container">
      <form @submit.prevent="agregarNota()" v-if="!editar">
        <h3 class="text-center">Agregar nueva Nota</h3>
        <input type="text" placeholder="Ingrese un Codigo" class="form-control my-2" v-model="nota.cod">
        <input type="text" placeholder="Ingrese un Nombre" class="form-control my-2" v-model="nota.nombre">
        <input type="text" placeholder="Ingrese una descripcion" class="form-control my-2" v-model="nota.descripcion">
        <b-button class="btn-sm btn-block btn-success" type="submit">Agregar</b-button>
      </form>
    </div>
    <br>

    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripción</th>
          <th scope="col">Fecha</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{item.cod}}</th>
          <td>{{item.nombre}}</td>
          <td>{{item.descripcion}}</td>
          <td>{{item.date}}</td>
          <td>
            <b-button class="btn-warning btn-sm mx-2" @click="activarEdicion(item._id)">Editar</b-button>
            <b-button class="btn-danger btn-sm mx-2" @click="eliminarNota(item._id)">Eliminar</b-button>
            <!--
        <b-button class="btn-warning btn-sm mx-2" @click="alert()">Actualizar</b-button>
        <b-button class="btn-danger btn-sm mx-2" @click="alert2()">Eliminar</b-button> -->
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        notas: [],
        mensaje: {color: '', texto: ''},
        nota: {cod: '', nombre: '', descripcion: ''},
        dismissSecs: 2,
        dismissCountDown: 0,
        editar: false,
        notaEditar: {}
      };
    },
    created() {
      this.listarNotas();
    },
    methods: {

      alert() {
        this.mensaje.color;
        this.mensaje.texto;
        this.showAlert();
      },

      listarNotas() {
        this.axios.get('/nota')
          .then((res) => {
            console.log(res.data);
            this.notas = res.data;
          })
          .catch((e) => {
            console.log('error' + e.response);
          })
      },

      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },

      showAlert() {
        this.dismissCountDown = this.dismissSecs
      },

      agregarNota() {
        this.axios.post('nueva-nota', this.nota)
          .then(res => {
            this.notas.push(res.data)
            this.nota.cod = '';
            this.nota.nombre = '';
            this.nota.descripcion = '';

            // Agregando alertas en la vista
            this.mensaje.color = 'success';
            this.mensaje.texto = 'Nota Agregada';
            this.showAlert();
          })
          .catch(e => {
            if(e.response.data.error.errors.nombre.message){
              this.mensaje.texto = e.response.data.error.errors.nombre.message;
            }else{this.mensaje.texto = 'Error de Sistema !!!!';}
            // Agregando alertas en la vista
            this.mensaje.color = 'danger';
            this.mensaje.texto = 'Ocurrio un Error al intentar agregar la Nota !!!';
            this.showAlert();
          })
      },

      eliminarNota(_id) {
        this.axios.delete(`nota/${_id}`)
          .then(res => {
            let index = this.notas.findIndex(item => item._id === res.data._id)
            this.notas.splice(index, 1);

            this.showAlert();
            this.mensaje.texto = 'Notas Eliminada!'
            this.mensaje.color = 'danger'
          })
          .catch(e => {
            console.log(e.response);
          })
      },

      activarEdicion(_id){
        this.editar = true;
        //console.log(_id)
        this.axios.get(`/nota/${_id}`)
          .then(res => {
            this.notaEditar = res.data;
          })
          .catch(e => {
            console.log(e.response);
          })
      },

      editarNota(item){
        //console.log(item);
        this.axios.put(`/nota/${item._id}`, item)
          .then(res => {
            const index = this.notas.findIndex(itemnes => itemnes._id === res.data._id);
            this.notas[index].cod = this.res.data.cod;
            this.notas[index].nombre = this.res.data.nombre;
            this.notas[index].descripcion = this.res.data.descripcion; 
           
            this.mensaje.color = 'success';
            this.mensaje.texto = 'Notas Editada!';
            this.showAlert();
           
          })
          .catch(e => {
            console.log(e.response);
          })
        this.editar = false;     
      }
    }
  };
</script>